<template>
	<div class="finishOrder">
		<div class="header-fixed">
			<header-top>
				<div class="left" slot="menu">
					<router-link to="/home" class="text-icon-options toHome">
						<img src="../../assets/image/icon-homepage.png">
						<span>{{lang('首页')}}</span>
					</router-link>
				</div>
				<div class="mdui-center mdui-valign point-progress" slot="title">
					<ul>
						<li class="active">
							<i>1</i>
						</li>
						<li class="active"></li>
						<li class="active">
							<i>2</i>
						</li>
						<li class="active"></li>
						<li class="active">
							<i>3</i>
						</li>
					</ul>
					<ul>
						<li class="active">
							<i>{{lang('确认商品')}}</i>
						</li>
						<li class="active">
							<i>{{lang('填写资料')}}</i>
						</li>
						<li class="active">
							<i>{{lang('订单完成')}}</i>
						</li>
					</ul>
				</div>
			</header-top>
		</div>
		<div style="height: 40px;"></div>
		<div class="mdui-order">
			<div class="mdui-text-center">
				<img class="success" src="../../assets/image/icon-success.png">
			</div>
			<div class="mdui-text-center mdui-m-y-1" v-if="orderInfo.payment !== '1'">
				<span>{{lang('下单成功，我们将尽快为您配送！')}}</span>
			</div>
			<div class="mdui-text-center mdui-m-y-1" v-else-if="orderInfo.payment === '1'&&!cancelledFlag&&!paySuccess&&$route.name==='finish'">
				<span>{{lang('下单成功，请您进行付款！')}}</span>
			</div>
			<div class="mdui-text-center mdui-m-y-1" v-show="paySuccess||$route.name==='success'">
				<span>{{lang('付款成功！')}}</span>
				<span>您已支付成功，再<a href="/home" style="color: #ce2424;">去逛逛</a></span>
			</div>
			<div class="mdui-text-center mdui-m-y-1" v-show="cancelledFlag && !paySuccess">
				<!--<span>{{lang('下单成功，请您进行付款！')}}</span>-->
				<span>您取消了付款，请重新付款</span>
			</div>
			<div class="mdui-paypal-btn" v-if="orderInfo.payment === '1'&&!paySuccess&&$route.name==='finish'">
				<form action="https://www.paypal.com/cgi-bin/webscr" method="post" id="sform">
					<input type="hidden" name="cmd" value="_xclick">
					<input type="hidden" name="business" :value="siteInfo.Paypal.paycode">
					<input type="hidden" name="item_name" :value="orderInfo.clientname">
					<input id='item_number' type="hidden" name="item_number" value="1">
					<input id='amount' type="hidden" name="amount" :value="orderInfo.orderprice">  <!-- 金额  -->
					<input type="hidden" name="currency_code" :value="currency">
					<input type='hidden' name='return' :value='successUrl'>
					<input type='hidden' name='notify_url' :value='orderUrl'>
					<input type='hidden' name='cancel_return' :value='successUrl'>
					<input id='invoice' type='hidden' name='invoice' :value='orderInfo.orderid'> <!-- 发票编码 注意：不能提交两次一样的发票编码 -->
					<input type='hidden' name='charset' value='utf-8'><!-- 字符集 -->
					<input type="hidden" name="no_shipping" value="1"> <!-- 不要求客户提供收货地址 -->
					<input type="hidden" name="no_note" value="remark Recharge">
					<input type="hidden" name="bn" value="IC_Sample">
					<input type='hidden' name='rm' value='2'>
					<input type="image" src='https://shop.static.zxkf.cc/paypal.svg' name="submit" alt="payments with payPal!" class="paypal-img">
				</form>
			</div>
		</div>
		<div class="mdui-detail">
			<div class="detail" v-if="orderInfo.orderid">
				<div class="mdui-ordersn">
					<span>{{lang('订单号')}} : {{orderInfo.orderid}}</span>
					<button
							class="mdui-btn mdui-color-default mdui-btn-copy"
							data-clipboard-text="XH1073827872"
							v-clipboard:copy="copyUrl"
							v-clipboard:success="onCopy"
							v-clipboard:error="onErro"
							@click="copyId()"
					>{{lang('复制')}}
					</button>
				</div>
				<span v-if="orderInfo.payment !== '1'||(paySuccess?paySuccess:orderInfo.succeed)">{{lang('下单成功，我们将尽快为您配送！')}}</span>
				<!--<span>{{lang('下单成功，我们将尽快为您配送！')}}</span>-->
				<div class="picList">
					<div class="picBox">
						<img :src="item?config.cdnbase+item:''" v-for="(item,index) of this.imglist" width="80" :key="index">
					</div>
				</div>
			</div>
		</div>
		<div class="mdui-text-center mdui-order-tip">
			<a :href="orderInfo.pay_receipt_url" v-if="orderInfo.pay_receipt_url" class="pay-detail">{{lang('查看付款凭证')}}</a>
			<a :href="returnUrl" v-if="paySuccess" class="pay-detail">{{lang('查看付款凭证')}}</a>
			<span>
				{{lang('建议保存')}}
				<span class="mdui-order-tip-text">{{lang('订单号')}}</span>
				{{lang('和')}}
				<span class="mdui-order-tip-text">{{lang('商城链接')}}</span>，{{lang('方便查询订单')}}
			</span>
		</div>
		<div class="marsk" ref="marsk" style="display: none"></div>
	</div>
</template>

<script>
  import {mapState, mapMutations} from "vuex";
  import {order} from "../../lib/goods";

  export default {
    data() {
      return {
        copyUrl: "",
        orderid: "",
        paypal: {
          sandbox: '',
          production:  ''
        },
        myStyle: {
          label: 'checkout',
          size: 'medium',    // small | medium | large | responsive
          shape: 'rect',     // pill | rect
          color: 'gold',     // gold | blue | silver | black
          tagline: false
        },
        amount: 0, //paypal价格
        currency: '', //paypal货币
        cancelledFlag:false, //取消付款
        paySuccess:false, //付款成功
        returnUrl:'',
        sandboxFlag:'',
		orderUrl: '',
		imglist: '',
		successUrl: ''
      }
    },
    created() {
    this.imglist = this.$store.state.imglist
      this.paypal.sandbox = this.siteInfo.Paypal?this.siteInfo.Paypal.paycode:''
      this.paypal.production = this.siteInfo.Paypal?this.siteInfo.Paypal.paycode:''
      this.amount = this.orderInfo.orderprice + ''
      this.currency = 'TWD'
      if (this.siteInfo.money === 'NT') {
        this.currency = 'TWD'
      } else if (this.siteInfo.money === 'HK') {
        this.currency = 'HKD'
      } else if (this.siteInfo.money === 'RM') {
        this.currency = 'MYR'
      } else if (this.siteInfo.money === '฿') {
        this.currency = 'THB'
      } else if (this.siteInfo.money === 'Rp') {
        this.currency = 'INR'
      } else if (this.siteInfo.money === '円') {
        this.currency = 'JPY'
      }
      process.env.NODE_ENV === 'development'?this.sandboxFlag='sandbox' : this.sandboxFlag='production'
		if (!this.orderInfo) {
			this.$router.push("/home");
		}
	},
    mounted() {
      this.orderUrl = `${window.location.origin}/api/shop/paypalPayment`
		this.successUrl = `${window.location.origin}/finishOrder`
    },
    destroyed: function () {
      this.$store.state.imglist = [];
      this.$store.state.orderId = '';
    },
    methods: {
    ...mapMutations(['setOrder']),
      copyId() {
        this.copyUrl = this.$store.state.orderId;
      },
      onCopy() {
        this.$toast.bottom(this.lang("复制成功"));
      },
      onErro() {
        this.$toast.bottom(this.lang("复制失败"));
      },
      async paymentCompleted({response,data}){
        this.$refs.marsk.style.display = "block";
        this.$loading("loading...");
        let orderInfo = await order.submitOrderPaypal({
          clientinfo:{
            payment: 1
          },
          orderid: this.$store.state.orderId,
          create_time: response.create_time,
          price: response.transactions[0].amount.total,
          currency: response.transactions[0].amount.currency,
          pay_id: data.payerID,
          paymentID: data.paymentID,
          paymentToken: data.paymentToken,
          returnUrl: data.returnUrl,
          orderidP: data.orderID,
          type: 'paypal',
          state:1
        })
        if(orderInfo.Error){
          this.$refs.marsk.style.display = "none";
          this.$loading.close();
          let languageInfo = this.siteInfo.language;
          return this.$toast.bottom(orderInfo.Info[languageInfo]);
        }else{
          this.$loading.close();
          this.$refs.marsk.style.display = "none";
          this.cancelledFlag = false
          this.paySuccess = true;
          this.returnUrl = data.returnUrl;
          //支付成功删除购物车商品
          let cartinfo = JSON.parse(localStorage.getItem("cart_info"));
          for (var i = 0; i < cartinfo.list.length; i++) {
            if (cartinfo.list[i].checked) {
              // 注意对比这行代码：删除元素后调整i的值
              cartinfo.list.splice(i--, 1);
            }
          }
          localStorage.setItem("cart_info", JSON.stringify(cartinfo));
          localStorage.removeItem('orderInfo')
        }
      },
      paymentCancelled(){
        this.cancelledFlag = true
      }
    },
    computed: {
      ...mapState(["language", "siteInfo", "orderInfo", "config"]),
      lang() {
        return function (src) {
          return this.language ? this.language[src] : "";
        };
      }
    }
  };
</script>

<style scoped lang="stylus">
	.finishOrder
		.mdui-order
			background: #fff;
			padding: 24px 0px 12px 0px;
			height: auto;
			box-sizing: border-box;
			img.success
				width: 50px
				height: 50px
			.mdui-paypal-btn
				text-align center
		.mdui-detail
			padding: 4%;
			background: #FFFCEE;
			.detail:first-child
				margin-top 0
			.mdui-ordersn
				margin-top: 5px;
				span
					font-size: 14px;
					color: #363636;
					line-height: 14px;
				.mdui-btn-copy
					color: #A59672;
					min-width: initial;
					width: 40px;
					text-align: center;
					padding: 0px;
					height: 22px;
					border: 1px solid #EDEDED;
					border-radius: 2px;
					line-height: 22px;
					font-size: 12px;
					margin-left: 5px;
			.mdui-ordersn + span
				display: block;
				margin-top: 6px;
				font-size: 12px;
				color: #979797;
				line-height: 12px;
			.picList
				overflow-x: auto;
				margin-top: 16px;
				.picBox
					display: flex
				&::-webkit-scrollbar
					display none
				img
					height: 80px
					margin-right 5px
		.mdui-order-tip
			background: #fff;
			color: #7B7B7B;
			padding: 4px 0 8px 0;
			font-size: 12px;
			.mdui-order-tip-text
				color: #CE2424;
				margin: 0px 4px;
		.point-progress
			flex-direction: column;
			width: 56%;
			padding-bottom: 3px;
			padding-left: 0
			display: flex
			padding-right 10%
			& > *
				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;
			ul
				display: flex;
				align-items: center;
				justify-content: space-between;
				width: 100%;
				li
					i
						transform: scale(.8);
						text-align: center;
						margin: 0 auto;
						display: block;
						font-size: 0;
						color: #000;
						font-style: normal;
						line-height: 16px;
					&.active
						i
							color: #fff;
							font-size: 12px;
			ul:first-child
				width: calc(100% - 36px);
				li:nth-child(2n+1)
					border-radius: 50%;
					background: #F8E9EA;
					height: 10px;
					width: 10px;
					&.active
						height: 16px;
						width: 16px;
						background: #B4272D;
				li:nth-child(2n)
					border-bottom: 3px solid #F8E9EA;
					flex: 1;
					&.active
						border-color: #B4272D;
			ul:last-child
				li
					i
						font-size: 12px;
						color: #777;
					&.active i
						color: #000;
		.header-fixed
			position: fixed
			top: 0
			left: 0
			right: 0
			z-index 100
			background-color: #fff
			border-bottom: 1px solid #ededed
			max-width 768px
			margin auto
			.left
				display: flex;
				height: 100%;
				align-items: end;
				.text-icon-options
					position: relative;
					height: 24px;
					width: 36px;
					display: inline-block;
					margin-top: 2px;
					margin-left: 0;
					& > img
						width: 20px;
						height: auto;
						margin-left: -10px;
						position: absolute;
						left: 50%;
						top: 0px;
					& > span
						width: 100%;
						text-align: center;
						margin: 0 auto;
						display: block;
						font-size: 10px;
						line-height: 12px;
						color: #363636;
						position: absolute;
						bottom: -12px;
	.pay-detail
		color: #b4272d;
		display block
		margin 10px
		font-size 14px
	.marsk {
		background: rgba(0, 0, 0, 0.6);
		position: fixed;
		width: 100vw;
		top: 0;
		bottom: 0;
		background: rgba(0, 0, 0, 0.6);
		z-index: 9999;
		max-width: 750px;
		box-sizing: border-box;
	}
	.paypal-img {
		width: 114px;
		margin: 0 auto;
		height: 35px;
		display: block;
		border-radius: 3px;
		background: #ffc439;
		box-shadow: 5px 5px 5px 0 #CCD;
	}
</style>
